.input {
  min-width: 50vw;
  max-width: 80vw;
  border: none;
  background-color: #000;
  color: #faebd7;
  caret-color: transparent;
  font-family: Consolas;
  font-size: 1rem;
}

.input:focus {
  outline: none;
}

.input::placeholder {
  color: #faebd7;
  font-weight: bolder;
}

.blink[type="text"] {
  animation: blinker 1s step-end infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}
